import React from "react";
import ReactApexChart from "react-apexcharts";
import "../App.css";

function Graph() {
    const series = [
        {
            name: 'Cases',
            data: [555, 12038, 69030, 88369, 167470, 932650, 2055424, 3343777, 4101699]
        }, 
        {
            name: 'Recovered',
            data: [28, 284, 9394, 42710, 76026, 191874, 501550, 1029651, 1376871]
        }, 
        {
            name: 'Deaths',
            data: [17, 259, 1666, 2996, 6472, 49675, 140659, 238619, 282709]
        }
    ];

    const options =  
    {
        chart: {
            
        },
        dataLabels: {
            enabled: false
        },
        stroke: {
          curve: 'smooth'
        },
        xaxis: {
            type: 'datetime',
            categories: [
                "1/22/20", 
                "2/1/20", 
                "2/15/20", 
                "3/1/20", 
                "3/15/20", 
                "4/1/20", 
                "4/15/20", 
                "5/1/20", 
                "5/7/20"
            ]
        },
        tooltip: {
            x: {
                format: 'dd/MM/yy'
            },
        },
    };

    return (
        <div
            style={{
                backgroundColor: "white",
                textAlign: "center",
            }}
        >
            <h2
                style={{ textAlign: "center", marginTop: "30px" }}
            >
                Covid-19 Global Graphs
            </h2>
            <br />
            <ReactApexChart 
                options={options} 
                series={series} 
                type="area" 
                height={350}
            />
            <ReactApexChart 
                options={options} 
                series={series} 
                type="bar" 
                height={350}
            />
        </div>
    );      
}

export default Graph;