import "./Css/MapView.scss"
import React from "react";
import { Map, TileLayer, Marker, Popup, ZoomControl } from "react-leaflet";
import { divIcon } from "leaflet";

/*
1. 1-100 => pink, super very small
2. 101-500 => pink, very small
3. 501-1000 => pink, small
4. 1001-5000 => purple, normal
5. 5001-10000 => purple big
6. 10001-50000 => red, very big
7. 50000 up => red, super very big
*/
//const icon = divIcon({className: 'marker-icon', iconSize: [32, 32]});
const markerIcons = {
    xxSmall: divIcon({className: 'map-view__marker pink', iconSize: [20, 20]}),
    xSmall: divIcon({className: 'map-view__marker pink', iconSize: [26, 26]}),
    Small: divIcon({className: 'map-view__marker pink', iconSize: [32, 32]}),
    normal: divIcon({className: 'map-view__marker purple', iconSize: [48, 48]}),
    large: divIcon({className: 'map-view__marker purple', iconSize: [64, 64]}),
    xlarge: divIcon({className: 'map-view__marker red', iconSize: [80, 80]}),
    xxlarge: divIcon({className: 'map-view__marker red', iconSize: [96, 96]}),
}
/*
var myIcon = L.icon({
    iconUrl: 'data:image/png;base64,',
    iconSize: [25, 41],
    iconAnchor: [12.5, 41],
    popupAnchor: [0, -41]
})*/

let selectedMarkerIcons = {};
Object.keys(markerIcons).forEach((key) => {
    const { className, iconSize } = markerIcons[key].options;
    //console.log(className);
    const icon = divIcon({
        className: `${className} selected`, iconSize
    });
    selectedMarkerIcons[key] = icon;
});

const maxBounds = [[90, 270], [-90, -240]];

function MapView(props) {
    const {
        viewport,
        locationArray,
        selectedLocation,
        onViewportChanged,
        onSelectMarker
    } = props;
    
    const markerElements = locationArray.map(data => {
        //id, country, iso2, lat, long, cases, deaths, recovered
        const {
            country,
            countryInfo: { _id, iso2, lat, long },
            cases, deaths, recovered, isHidden
        } = data;

        if (!!isHidden) return null;        

        let markerIconsSet = markerIcons;
        if (!!selectedLocation) {
            //console.log(data.country);
            //console.log(selectedLocation.country);            
            if (data.country === selectedLocation.country) {
                markerIconsSet = selectedMarkerIcons;
            }
        }

        let markerIcon = markerIconsSet.xxSmall;

        //let markerIcon = icons.xxSmall;
        if(cases >= 101 && cases <= 500){
            markerIcon = markerIconsSet.xSmall;
        }
        if(cases >= 501 && cases <= 1000){
            markerIcon = markerIconsSet.Small;
        }
        if(cases >= 1001 && cases <= 5000){
            markerIcon = markerIconsSet.normal;
        }
        if(cases >= 5001 && cases <= 10000){
            markerIcon = markerIconsSet.large;
        }
        if(cases >= 10001 && cases <= 50000){
            markerIcon = markerIconsSet.xlarge;
        }
        if(cases >= 50001){
            markerIcon = markerIconsSet.xxlarge;
        }
        //id, country, iso2, lat, long, cases, deaths, recovered
        let title = country;
        return (
            <Marker 
                key={`${_id}-${country}`}
                position={[lat, long]}
                icon={markerIcon}
                onclick={_ => onSelectMarker(_id, country, iso2, lat, long, cases, deaths, recovered)}
                onmouseover={e => e.target.openPopup()}
                onmouseout={e => e.target.closePopup()}
            >
                <Popup autoPan={false}>
                    <b>{title}</b>
                </Popup>
            </Marker>
        )
    });

    return (
        <Map 
            className="map-view" 
            viewport={viewport}
            zoomControl={false}
            maxBounds={maxBounds}
            maxBoundsViscosity={1.0}
            minZoom={2}
            onViewportChanged={onViewportChanged}
        >
            <ZoomControl position="topright" />
            <TileLayer
            //attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
            //url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            attribution="© <a href='https://www.mapbox.com/about/maps/'>Mapbox</a> © <a href='http://www.openstreetmap.org/copyright'>OpenStreetMap</a> <strong><a href='https://www.mapbox.com/map-feedback/' target='_blank'>Improve this map</a></strong>"
            url="https://api.mapbox.com/styles/v1/leesungwoo/cka88qgrk07de1inr06ivi51i/tiles/256/{z}/{x}/{y}@2x?access_token=pk.eyJ1IjoibGVlc3VuZ3dvbyIsImEiOiJja2E4YTQ0dWcwMm9xMnBxanJsdTFscjZnIn0.df-RbvF_TI8O3sun45xx5w"
            />
            {markerElements}
        </Map>
    );
}

export default MapView;