import React, { useState, useEffect, useCallback, useRef } from "react";
import ListView from "./ListView";
import MapView from "./MapView";
import "leaflet/dist/leaflet.css";
import "../Css/App.scss";

import Axios from "axios";
//import DetailsView from "./DetailsView";
import DetailsView2 from "./DetailsView2";
import api from "../Api"
import LoadingView from "./LoadingView";
import ErrorView from "./ErrorView";
import FilterView from "./FilterView";
import InfoView from "./InfoView";

//const api = 'https://coronavirus-tracker-api.herokuapp.com/v2/locations';
//const api = 'https://corona.lmao.ninja/v2/countries';
//const api2 = 'https://corona.lmao.ninja/v2/all';

const mqDesktop = 1024;

function MapFrame() {
    const [locationArray, setLocationArray] = useState([]);
    const [selectedLocation, setSelectedLocation] = useState(null);
	const [totalArray, setTotalArray] = useState([]);
	//const [mapCenter, setMapCenter]= useState([13, 100]);
    const [mapViewport, setMapViewport] = useState({ center: [15, 101], zoom: 5 });

    const [isAllLocationLoading, setIsAllLocationLoading] = useState(true);
    const [isLocationLoading, setIsLocationLoading] = useState(false);
    const [isShowFilter, setIsShowFilter] = useState(false);
    const [isNeedResetFilter, setIsNeedResetFilter] = useState(false);
    const [isShowInfo, setIsShowInfo] = useState(false);
    
    const [error, setError] = useState(null);

    const appRef = useRef(null);

    const [dcases, setDcases] = useState(null);
    //const [ddeaths, setDdeaths] = useState("");
    //const [drecovered, setDrecovered] = useState("");

    // - Filter
	const onFilterLocations = useCallback((nextLocationArray) => {
		setSelectedLocation(null);
		setLocationArray(nextLocationArray);
		setIsShowFilter(false);
	}, []);

	const onResetFilterLocations = useCallback(() => {
		setSelectedLocation(null);
		setLocationArray(prevArray => {
			return prevArray.map(location => {
				const nextLocation = Object.assign({}, {...location});
				delete nextLocation.isHidden;
				return nextLocation;
			});
		});
		setIsShowFilter(false);
		setIsNeedResetFilter(true);
	}, []);

    const onResetFilterEnd = useCallback(() => setIsNeedResetFilter(false), []);
    
    function sortedLocationArray(locations) {
        return [...locations].sort((location1, location2) => {
            return location2.cases - location1.cases;
        });
    }

    // select
    /*const onSelectLocation = useCallback((id) => {
        const location = locationArray.find(_location => _location.countryInfo._id === id);
        //console.log(id);
        //console.log(location.countryInfo._id);
		if (location === undefined) {
			setSelectedLocation(null);
			return;
		}
		setSelectedLocation(location);
		const { countryInfo: { lat, long } } = location;
		setMapCenter([lat, long]);
	}, [locationArray]);
    */
	const onDeselectLocation = useCallback(() => {
		setSelectedLocation(null);
    }, []);
    
    

    const onSelectLocation = useCallback((id, country, iso2, lat, long, cases, deaths, recovered) => {
        // console.log(id);
		// console.log(iso2);
		// console.log(lat);
		// console.log(long);
		// console.log(cases);
		// console.log(deaths);
		// console.log(recovered);
		setIsLocationLoading(true);
        setError(null);
        
        setDcases([{
            // "latest": {
            //     "cases": cases,
            //     "deaths": deaths,
            //     "recovered": recovered
            // }
            "cases": cases,
            "deaths": deaths,
            "recovered": recovered
        }]);

        const location = locationArray.find(_location => _location.countryInfo._id === id);
        //console.log(id);
        //console.log(location.countryInfo._id);
		if (location === undefined) {
			setSelectedLocation(null);
			return;
		}
		
        
		api.getLocation(iso2).then(response => {
            //const { location } = response.data;
            //console.log(response.data);
            //setSelectedLocation(response.data);
			//const { coordinates: { latitude, longitude } } = location;
            
			let nextLatitude = lat;
			if (appRef.current.offsetWidth < mqDesktop) {
				if (lat >= 65) nextLatitude -= 0.5
				else if (lat < 65 && lat >= 50) nextLatitude -= 1;
				else if (lat < 50 && lat >= 45) nextLatitude -= 1.5;
				else nextLatitude -= 2;
			}
            
            setSelectedLocation(response.data);            
            setMapViewport({ center: [nextLatitude, long], zoom: 6 });
            
            //setSelectedLocation(location);
		    //const { countryInfo: { lat, long } } = location;
            //setMapCenter([lat, long]);
		}).catch(error => {
			setSelectedLocation(null);
			setError(error);
            /*console.error(error);
			setSelectedLocation(null);*/
		}).finally(() => {
			setIsLocationLoading(false);
		});
    }, [locationArray]);
    
    // - Open popup
	const onOpenFilter = useCallback(() => setIsShowFilter(true), []);
	const onCloseFilter = useCallback(() => setIsShowFilter(false), []);

	const onOpenInfo = useCallback(() => setIsShowInfo(true), []);
	const onCloseInfo = useCallback(() => setIsShowInfo(false), []);

	// - Close error
	const onCloseError = useCallback(() => setError(null), []);


    useEffect(() => {
        /*Axios.get(api).then(response => {
            const sortedLocation = sortedLocationArray(response.data)
            setLocationArray(sortedLocation);
        }).catch(error => {
            console.log(error);
        })
        */
        Axios
        .all([
          Axios.get("https://corona.lmao.ninja/v2/countries"),
          Axios.get("https://corona.lmao.ninja/v2/all")
        ])
        .then((response) => {
            const sortedLocation = sortedLocationArray(response[0].data)
            setLocationArray(sortedLocation);
            setTotalArray(response[1].data);
        })
        .catch((err) => {
            console.log(err);
            setError(err);
        })
        .finally(() => {
            setIsAllLocationLoading(false);
        });
        
        
    }, []);
    /*
    let detailsView = null;
    
	if (selectedLocation != null) {
        //console.log(selectedLocation);
        detailsView = <DetailsView2 
                        dcases={dcases}
                        isLoading={isLocationLoading}
                        selectedLocation={selectedLocation} 
                        onClickClose={onDeselectLocation} 
                      />
    }
    */
    return (
        <div ref={appRef}>
            <ListView 
                isLoading={isAllLocationLoading}
                totalArray={totalArray}
                locationArray={locationArray}
                selectedLocation={selectedLocation} 
				onSelectItem={onSelectLocation} 
                onDeselectItem={onDeselectLocation}
                onClickFilter={onOpenFilter}
                onClickReset={onResetFilterLocations}
                onClickInfo={onOpenInfo} />
            <MapView 
                viewport={mapViewport}
				selectedLocation={selectedLocation}
				onViewportChanged={setMapViewport}                
                locationArray={locationArray} 
                onSelectMarker={onSelectLocation}    
            />
            <DetailsView2 
                dcases={dcases}
                isLoading={isLocationLoading}
                selectedLocation={selectedLocation} 
                onClickClose={onDeselectLocation} 
            />
            <FilterView 
				isShow={isShowFilter}
				isNeedReset={isNeedResetFilter}
				locationArray={locationArray} 
				onClickFilter={onFilterLocations} 
				onClickClose={onCloseFilter}
				onResetEnd={onResetFilterEnd} />
            <InfoView isShow={isShowInfo} onClickClose={onCloseInfo} />
            <LoadingView
				isShow={isAllLocationLoading}
				label="Loading"
				extraClass="loading-view__app" />
            {/*detailsView*/}
            <ErrorView error={error} onClickClose={onCloseError} />
        </div>
    )
}

export default MapFrame;