import React from "react";
//import AppBodyData from "./AppBodyData";
import CallData from "./CallData";

class PostList extends React.Component {
    
    constructor(props){
        super(props)
        this.state = {
            items:[],
            isLoaded:false,
            direction: {
                cases: 'desc',
            }
        }

        this.sortBy = this.sortBy.bind(this)
    }    
    
    componentDidMount() {
        const url = "https://corona.lmao.ninja/v2/countries";
        fetch(url)
            .then(res => res.json())
            .then(json => {
                this.setState({
                    isLoaded: true,
                    items: json,
                    direction: json.sort( (a, b) => (
                        parseFloat(b['cases']) - parseFloat(a['cases'])
                    )),
                })
            });        
    }

    sortBy(key) {
        this.setState({
            items: this.state.items.sort( (a, b) => (
                this.state.direction[key] === 'desc'
                  ? parseFloat(b[key]) - parseFloat(a[key])
                  : parseFloat(a[key]) - parseFloat(b[key])
            )),
            direction: {
                [key]: this.state.direction[key] === 'desc'
                  ? 'asc'
                  : 'desc'
            }
        })
    }

    render() {

        var { isLoaded, items } = this.state;
        if(!isLoaded) {
            return <div>Loading...</div>
        } else {
            return (
                <div>
                    <CallData 
                        data={items}
                        sortBy={this.sortBy}
                    />
                </div>
            )
        }        
    }
}

export default PostList;