import React from "react";
import "../App.css";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';

function Symptom() {
    return (
        <div style={{ margin: "20px" }}>
            <Tabs>
                <TabList>
                <Tab>Overview</Tab>
                <Tab>Symptoms</Tab>
                <Tab>Prevwntion</Tab>
                <Tab>Treatments</Tab>
                </TabList>

                <TabPanel>
                <p>
                코로나바이러스감염증(코로나19)은 새롭게 발견된 코로나바이러스로 인해 발생하는 감염 질환입니다.
                </p>
                <p>
                코로나19에 감염되면 대부분 경증에서 중증 수준의 증상을 보이며 특별한 치료 없이도 질환으로부터 회복합니다.
                </p>
                <hr />
                <p>전염 방식</p>
                <p>코로나19를 유발하는 바이러스는 주로 감염자가 기침 또는 재채기를 하거나 숨을 내쉴 때 발생하는 비말을 통해 전염됩니다. 이 비말은 공기 중에 머물기에는 무겁기 때문에 바닥이나 표면으로 금방 떨어집니다.</p>
                <p>코로나19 감염자와 인접한 거리에서 호흡할 때 바이러스를 들이마시게 되거나 오염된 표면에 접촉한 손으로 눈, 코 또는 입을 만질 경우 감염될 수 있습니다.</p>
                <p>
                    Source:{' '}
                    <a 
                        href="https://www.who.int/emergencies/diseases/novel-coronavirus-2019" 
                        target="_blank"
                        rel="noopener noreferrer"
                        style={{ color: "blue" }}
                    >
                    Who.int
                    </a>
                </p>
                </TabPanel>
            </Tabs>
        </div>
    )   
}

export default Symptom;