import React, { useEffect, useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import axios from "axios";
import GoogleMapReact from 'google-map-react';
import mapStyles from "./mapStyles";

//import Marker from './Marker';
//import GoogleMap from './GoogleMap';

function Maps() {  
  //const [latest, setLatest] = useState([]);
  const [results, setResults] = useState([]);  

  useEffect(() => {
    axios
      .all([
        //axios.get("https://corona.lmao.ninja/v2/all"),
        axios.get("https://corona.lmao.ninja/v2/countries")
      ])
      .then((resposeArr) => {
        //setLatest(resposeArr[0].data);
        setResults(resposeArr[0].data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  
  //const date = new Date(parseInt(latest.updated));
  //const lastUpdated = date.toString();

  const countriesLocations = results.map((data, i) => {
    //const date = new Date(parseInt(data.updated));
    //const lastUpdated = date.toString();
    return ( 
      <div
        lat={data.countryInfo.lat} 
        lng={data.countryInfo.long}
        style={{
          color: "white",
          display: 'inline-flex',
          background: 'rgba(255, 0, 0, 0.2)',
          padding: '15px 10px',
          borderRadius: "100%"
          }}
        >
        {/*<small>{lastUpdated}</small>*/}
        <img width="20px" height="10px" src={data.countryInfo.flag} alt="" />{data.cases}
      </div>
    );
  });

  const AnyReactComponent = ({ text }) => (
    <div style={{
      color: 'white', 
      background: 'grey',
      padding: '15px 10px',
      display: 'inline-flex',
      textAlign: 'center',
      alignItems: 'center',
      justifyContent: 'center',
      borderRadius: '100%',
      transform: 'translate(-50%, -50%)'
    }}>
      {text}
    </div>
  );


  return (
        <div style={{ height: '100vh', width: '100%' }}>
            <GoogleMapReact
              bootstrapURLKeys={{ key: "xxxxxxx" }}
              defaultCenter={{ lat: 24.4539, lng: 54.3773 }}
              defaultZoom={4}
              options={{ styles: mapStyles.darkBlue }}
            >
            {countriesLocations}
            <AnyReactComponent 
              lat={59.955413} 
              lng={30.337844} 
              text={'Kreyser Avrora'} 
            />
            </GoogleMapReact>
        </div>
  );
}

export default Maps;