import React, { useState, useEffect } from 'react';
import axios from 'axios';
import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, { Search, CSVExport } from 'react-bootstrap-table2-toolkit';
import * as ReactBootstrap from "react-bootstrap";
//import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";
//import "react-bootstrap-table2-paginator/dist/react-bootstrap-table2-paginator.min.css";
//import "bootstrap/dist/css/bootstrap.min.css";
import "../App.css";
import filterFactory, { textFilter } from 'react-bootstrap-table2-filter';

function PagiNation(props) {
    
    const [Players, setPlayers] = useState([]);
    const [Loading, setLoading] = useState(false);
    
    const getPlayerData = async () => {
        try {
            const data = await axios.get(
                "https://corona.lmao.ninja/v2/countries"
            );
            console.log(data);
            setPlayers(data.data);
            setLoading(true);
        } catch (e) {
            console.log(e);
        }
    };
    
    function flagFormatter(cell, row) { 
        return (
          <img src={ cell } alt="" />
        );
    }

    function todayCasesFormatter(cell, row) { 
        if (row.todayCases > 0) {
            return (
              <span>
                <strong style={ { color: 'red' } }>+{ cell }</strong>
              </span>
            );
        }
    }
    function todayDeathsFormatter(cell, row) { 
        if (row.todayDeaths > 0) {
            return (
              <span>
                <strong style={ { color: 'red' } }>+{ cell }</strong>
              </span>
            );
        }
    }

    const headerSortingStyle = { backgroundColor: '#c8e6c9' };
    
    function countryFormatter(column, colIndex, { sortElement, filterElement }) {
        return (
          <div style={ { display: 'flex', flexDirection: 'column' } }>
            { filterElement }
            { column.text }
            { sortElement }
          </div>
        );
    }

    const columns = [
        { dataField: "countryInfo.flag", text: "Flag", sort: true, 
            formatter: flagFormatter
        },
        { dataField: "country", text: "Country", sort: true ,
            filter: textFilter({
                style:{
                    backgroundColor: "yellow"
                },
            }),
            headerFormatter: countryFormatter,
            style: (cell, row, rowIndex, colIndex) => {
                if (rowIndex % 2 === 0) {
                    return {
                        backgroundColor: '#81c784'
                    };
                }
                return {
                    backgroundColor: '#c8e6c9'
                };
            },
            headerSortingStyle
        },
        { 
            dataField: "cases", 
            text: "Cases", 
            sort: true,
            order: "desc",
            sortCaret: (order, column) => {
                if (!order) return (<span>&nbsp;&nbsp;Desc/Asc</span>);
                else if (order === 'asc') return (<span>&nbsp;&nbsp;Desc/<font color="red">Asc</font></span>);
                else if (order === 'desc') return (<span>&nbsp;&nbsp;<font color="red">Desc</font>/Asc</span>);
                return null;
            },
            headerSortingStyle
        },
        { dataField: "todayCases", text: "newCases", sort: true, 
            formatter: todayCasesFormatter 
        },
        { dataField: "deaths", text: "Deaths", sort: true },
        { dataField: "todayDeaths", text: "newDeaths", sort: true, 
            formatter: todayDeathsFormatter 
        },
    ];
    /*
    const options = {
        custom: true,
        totalSize: Players.length
    };
    */
    const defaultSorted = [{
        dataField: 'cases',
        order: 'desc'
    }];

    const rowStyle = (row, rowIndex) => {
        row.index = rowIndex;
        const style = {};
        if (rowIndex % 2 === 0) {
            style.backgroundColor = 'transparent';
        } else {
            style.backgroundColor = 'rgba(54, 163, 173, .10)';
        }
        style.borderTop = 'none';

        return style;
    }
    /*
    const rowEvents = {
        onMouseEnter: (e, row, rowIndex) => {
            setState({ hoverIdx: rowIndex });
        },
        onMouseLeave: () => {
            setState({ hoverIdx: null });
        }
    }
    */
    const rowEvents = {
        onClick: (e, row, rowIndex) => {
            console.log(`clicked on row with index: ${rowIndex}`);
        },
        onMouseEnter: (e, row, rowIndex) => {
            console.log(`enter on row with index: ${rowIndex}`);
        }
    };

    const expandRow = {
        renderer: () => (
          <div style={ { width: '100%', height: '20px' } }>Content</div>
        ),
        showExpandColumn: true,
        expandByColumnOnly: true
    };

    const { SearchBar, ClearSearchButton } = Search;
    const { ExportCSVButton } = CSVExport;    

    const CaptionElement = () => 
        <h3 style={{ 
            borderRadius: '0.25em', 
            textAlign: 'center', 
            color: 'purple', 
            border: '1px solid purple', 
            padding: '0.5em' }}>
                Component as Header
        </h3>;

    useEffect(() => {
            getPlayerData();
    }, []);

    const selectRow = {
        mode: 'radio',
        clickToSelect: true
    };
    ///////////////////////////////////////////////////////
    const customTotal = (from, to, size) => (
        <span className="react-bootstrap-table-pagination-total">
          Showing { from } to { to } of { size } Results
        </span>
    );
    /*
    const pageButtonRenderer = ({page, active, disable, title, onPageChange}) => {
        const handleClick = (e) => {
            e.preventDefault();
            onPageChange(page);
        };
        
        const activeStyle = {};
        if (active) {
          activeStyle.backgroundColor = 'black';
          activeStyle.color = 'white';
        } else {
          activeStyle.backgroundColor = 'gray';
          activeStyle.color = 'black';
        }
        if (typeof page === 'string') {
          activeStyle.backgroundColor = 'white';
          activeStyle.color = 'black';
        }
        return (
            <li className="page-item">
            <a href="/#" onClick={ handleClick } style={ activeStyle }>{ page }</a>
            </li>
        );
    };
    */
    const pageListRenderer = ({
        pages,
        onPageChange
      }) => {
        const pageWithoutIndication = pages.filter(p => typeof p.page !== 'string');
        return (
          <div>
            {
              pageWithoutIndication.map(p => (
                <button className="btn btn-success" onClick={ () => onPageChange(p.page) }>{ p.page }</button>
              ))
            }
          </div>
        );
    };
    
    const sizePerPageRenderer = ({
        options,
        currSizePerPage,
        onSizePerPageChange
      }) => (
        <div className="btn-group" role="group">
          {
            options.map((option) => {
              const isSelect = currSizePerPage === `${option.page}`;
              return (
                <button
                  key={ option.text }
                  type="button"
                  onClick={ () => onSizePerPageChange(option.page) }
                  className={ `btn ${isSelect ? 'btn-secondary' : 'btn-warning'}` }
                >
                  { option.text }
                </button>
              );
            })
          }
        </div>
    );
    
    const options = {
        pageListRenderer,
        sizePerPageRenderer,
        paginationSize: 10,
        pageStartIndex: 1,
        // alwaysShowAllBtns: true, // Always show next and previous button
        // withFirstAndLast: false, // Hide the going to First and Last page button
        // hideSizePerPage: true, // Hide the sizePerPage dropdown always
        // hidePageListOnlyOnePage: true, // Hide the pagination list when only one page
        firstPageText: 'First',
        prePageText: 'Back',
        nextPageText: 'Next',
        lastPageText: 'Last',
        nextPageTitle: 'First page',
        prePageTitle: 'Pre page',
        firstPageTitle: 'Next page',
        lastPageTitle: 'Last page',
        showTotal: true,
        paginationTotalRenderer: customTotal,
        disablePageTitle: true,
        sizePerPageList: [{
          text: '10', value: 10
        }, {
          text: '20', value: 20
        }, {
          text: 'All', value: Players.length
        }] // A numeric array is also available. the purpose of above example is custom the text
    };
    //////////////////////////////////////////////////////
    return (
        <div>
            <ToolkitProvider
                keyField="countryInfo.flag"
                data={ Players }
                columns={ columns }
                search
            >
            {
                props => (
                    <div>
                        <h3>Input something at below input field:</h3>
                        <SearchBar { ...props.searchProps } />
                        <ClearSearchButton { ...props.searchProps } />
                        <hr />
                        {Loading ? ( 
                        <BootstrapTable
                            bootstrap4
                            classes="react-bootstrap-table"
                            defaultSorted={ defaultSorted }
                            keyField="countryInfo.flag"
                            data={Players}
                            caption={<CaptionElement />}
                            pagination={paginationFactory(options)}
                            columns={columns}
                            noDataIndication="There is no data"
                            filter={ filterFactory() }
                            rowStyle={ rowStyle }
                            rowEvents={ rowEvents }
                            selectRow={ selectRow }
                            expandRow={ expandRow }
                            filterPosition="top"
                            { ...props.baseProps }
                        />) : (
                            <ReactBootstrap.Spinner animation="border" />
                        )}
                        <ExportCSVButton { ...props.csvProps }>Export CSV!!</ExportCSVButton>
                    </div>
                )
            }
            </ToolkitProvider>
        </div>
    );//return end
}

export default PagiNation;