import { useState, useEffect } from 'react'
import Axios from 'axios';

function useStats(url) {
    const [stats, setStatas] = useState();
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState();
    useEffect(() => {
        Axios
        .all([
            Axios.get(url)
        ])
        .then((resposeArr) => {
            setLoading(true);
            setError();
            setStatas(resposeArr[0].data);
            setLoading(false);
        })
        .catch((err) => {
            setError(err);
        });
    }, [url]);
    return {
        stats,
        loading,
        error,
    };
}

export default useStats;
