import React from "react";
import L from "leaflet";
import { Map, TileLayer, Marker, Popup } from "react-leaflet";
import "../App.css";
import leafGreen from "../assets/leaf-green.png";
import leafOrange from "../assets/leaf-orange.png";
import leafRed from "../assets/leaf-red.png";
import leafShadow from "../assets/leaf-shadow.png";

import GeojsonLayer from '../Components/GeojsonLayer';

var myIcon = L.icon({
    iconUrl: 'data:image/png;base64,',
    iconSize: [25, 41],
    iconAnchor: [12.5, 41],
    popupAnchor: [0, -41]
})

class LeafletMap extends React.Component {

    constructor(props){
        super(props)
        this.state = {
            items:[],
            greenIcon: {
                lat: 35.843965,
                lng: -78.7851395,
            },        
            orangeIcon: {
                lat: 35.774416,
                lng: -78.633271,
            },        
            redIcon: {
                lat: 35.772790,
                lng: -78.652305,
            },        
            zoom: 4,
        }
    }
    
    componentDidMount() {
        const url = "https://corona.lmao.ninja/v2/countries";
        fetch(url)
            .then(res => res.json())
            .then(json => {
                this.setState({items: json})
            });    
    }
    /*
    state = {
        greenIcon: {
            lat: 35.843965,
            lng: -78.7851395,
        },        
        orangeIcon: {
            lat: 35.774416,
            lng: -78.633271,
        },        
        redIcon: {
            lat: 35.772790,
            lng: -78.652305,
        },        
        zoom: 10,
    }
    */
    greenIcon = L.icon({
        iconUrl: leafGreen,
        shadowUrl: leafShadow,
        iconSize:     [38, 95],
        shadowSize:   [50, 64],
        iconAnchor:   [22, 94],
        shadowAnchor: [4, 62],
        popupAnchor:  [-3, -76]
    });

    orangeIcon = L.icon({
        iconUrl: leafOrange,
        shadowUrl: leafShadow,
        iconSize:     [38, 95],
        shadowSize:   [50, 64],
        iconAnchor:   [22, 94],
        shadowAnchor: [4, 62],
        popupAnchor:  [-3, -76]
    });

    redIcon = L.icon({
        iconUrl: leafRed,
        shadowUrl: leafShadow,
        iconSize:     [38, 95],
        shadowSize:   [50, 64],
        iconAnchor:   [22, 94],
        shadowAnchor: [4, 62],
        popupAnchor:  [-3, -76]
    });
    
    render() {

        const positionGreenIcon = [this.state.greenIcon.lat, this.state.greenIcon.lng];
        //const positionOrangeIcon = [this.state.orangeIcon.lat, this.state.orangeIcon.lng];
        //const positionRedIcon = [this.state.redIcon.lat, this.state.redIcon.lng];

        var { items } = this.state;

        return (
            <Map 
                className="map" 
                center={positionGreenIcon} 
                zoom={this.state.zoom}
                zoomControl={false}
            >
                <TileLayer
                    //attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                    //url="https://tiles.stadiamaps.com/tiles/alidade_smooth_dark/{z}/{x}/{y}{r}.png"
                    attribution="© <a href='https://www.mapbox.com/about/maps/'>Mapbox</a> © <a href='http://www.openstreetmap.org/copyright'>OpenStreetMap</a> <strong><a href='https://www.mapbox.com/map-feedback/' target='_blank'>Improve this map</a></strong>"
            url="https://api.mapbox.com/styles/v1/leesungwoo/cka88qgrk07de1inr06ivi51i/tiles/256/{z}/{x}/{y}@2x?access_token=pk.eyJ1IjoibGVlc3VuZ3dvbyIsImEiOiJja2E4YTQ0dWcwMm9xMnBxanJsdTFscjZnIn0.df-RbvF_TI8O3sun45xx5w"
                    detectRetina={true}
                    maxZoom={20}
                    maxNativeZoom={17}
                />
                <GeojsonLayer url="../data/world.json" />
                {/*
                <Marker position={positionGreenIcon} icon={this.greenIcon}>
                <Popup>
                    Green icon
                </Popup>
                </Marker>
                <Marker position={positionOrangeIcon} icon={this.orangeIcon}>
                <Popup>
                    Orange icon
                </Popup>
                </Marker>
                <Marker position={positionRedIcon} icon={this.redIcon}>
                <Popup>
                    Red icon
                </Popup>
                </Marker>
                */}
                {items.map((row, index) => {
                        //console.log(row.country);
                        return (
                                
                                <Marker
                                    key={index}
                                    position={[
                                        row.countryInfo.lat,
                                        row.countryInfo.long
                                    ]}
                                    icon={myIcon}
                                    //icon={this.greenIcon}
                                >
                                    <Popup>
                                        {row.country}
                                    </Popup>
                                </Marker>
                        )
                })}
            </Map>
        )
    }
}

export default LeafletMap;