import React from 'react';
import NumberFormat from "react-number-format";

export default function CallData(props) {
    return (
        <table cellPadding="1" cellSpacing="1" border="1" width="100%">
            <thead>
                <tr align="center">
                    <th>img</th>
                    <th>Country</th>
                    <th width="100px">
                        <button onClick={ () => props.sortBy('cases') }>
                        Cases
                        </button>
                    </th>
                    <th width="100px">
                        <button onClick={ () => props.sortBy('todayCases') }>
                        New Cases
                        </button>
                    </th>
                    <th width="100px">Deaths</th>
                    <th width="100px">New Deaths</th>
                    <th width="100px">Recovered</th>
                    <th width="100px">Active</th>
                    <th width="100px">Critical</th>
                    <th width="100px">casesPerOneMillion</th>
                    <th width="100px">deathsPerOneMillion</th>
                    <th width="100px">Tests</th>
                    <th width="100px">testsPerOneMillion</th>
                </tr>
            </thead>
            <tbody>
                {props.data.map(function(row, index) {
                        return (
                            <tr key={index}>
                                <td><img src={row.countryInfo.flag} alt={row.country} /></td>
                                <td align="center">{row.country}</td>
                                <td align="right">
                                <NumberFormat 
                                    value={row.cases} 
                                    displayType={'text'} 
                                    thousandSeparator={true}
                                    style={{ fontSize: "14px" }}
                                />
                                </td>
                                <td align="right">
                                {
                                    row.todayCases > 0
                                    && '+'
                                }            
                                <NumberFormat 
                                    value=
                                        {
                                            row.todayCases > 0
                                            && row.todayCases
                                        }
                                    displayType={'text'} 
                                    thousandSeparator={true}
                                    style={{ fontSize: "14px" }}
                                />
                                </td>
                                <td align="right">
                                <NumberFormat 
                                    value={row.deaths} 
                                    displayType={'text'} 
                                    thousandSeparator={true}
                                    style={{ fontSize: "14px" }}
                                />
                                </td>
                                <td align="right">
                                {
                                    row.todayDeaths > 0
                                    && '+'
                                }            
                                <NumberFormat 
                                    value=
                                    {
                                        row.todayDeaths > 0
                                        && row.todayDeaths
                                    }
                                    displayType={'text'} 
                                    thousandSeparator={true}
                                    style={{ fontSize: "14px" }}
                                />
                                </td>
                                <td align="right">
                                <NumberFormat 
                                    value={row.recovered} 
                                    displayType={'text'} 
                                    thousandSeparator={true}
                                    style={{ fontSize: "14px" }}
                                />
                                </td>
                                <td align="right">
                                <NumberFormat 
                                    value={row.active} 
                                    displayType={'text'} 
                                    thousandSeparator={true}
                                    style={{ fontSize: "14px" }}
                                />
                                </td>
                                <td align="right">
                                <NumberFormat 
                                    value={row.critical} 
                                    displayType={'text'} 
                                    thousandSeparator={true}
                                    style={{ fontSize: "14px" }}
                                />
                                </td>
                                <td align="right">
                                <NumberFormat 
                                    value={row.casesPerOneMillion} 
                                    displayType={'text'} 
                                    thousandSeparator={true}
                                    style={{ fontSize: "14px" }}
                                />
                                </td>
                                <td align="right">
                                <NumberFormat 
                                    value={row.deathsPerOneMillion} 
                                    displayType={'text'} 
                                    thousandSeparator={true}
                                    style={{ fontSize: "14px" }}
                                />
                                </td>
                                <td align="right">
                                <NumberFormat 
                                    value={row.tests} 
                                    displayType={'text'} 
                                    thousandSeparator={true}
                                    style={{ fontSize: "14px" }}
                                />
                                </td>
                                <td align="right">
                                <NumberFormat 
                                    value={row.testsPerOneMillion} 
                                    displayType={'text'} 
                                    thousandSeparator={true}
                                    style={{ fontSize: "14px" }}
                                />
                                </td>
                            </tr>
                        )
                    }
                )}
            </tbody>
        </table>
    )
}